import { Skeleton } from 'antd';
import React, {lazy,Suspense} from 'react';
import {
    BrowserRouter as Router,
    Redirect,
    Route,
  } from "react-router-dom";
  import jwtDecode from 'jwt-decode';

const HomeAdmin = lazy(() => import('./pages/AdminHome'));
const AddTest = lazy(() => import('./pages/AddTest'));
const Test = lazy(() => import('./pages/Test'));
const EditTest = lazy(() => import('./pages/edit-test'));
const Question = lazy(() => import('./pages/question'));
const GoTest = lazy(() => import('./pages/GoTest'));
const LoginPage = lazy(() => import('./pages/Login'));
const Student = lazy(() => import('./pages/Student'));
const ListTest = lazy(() => import('./pages/ListTest'));
const ResultTest = lazy(() => import('./pages/Demo'));
const HinShop = () => {
    let token = localStorage.getItem('tokentest');
    let isAdmin = localStorage.getItem('isAdmin');
    let decode = token === null ? {} : jwtDecode(token);
    return(
        <Router>
            <Suspense
                fallback={<Skeleton active />}
            >
                {/* <Route exact path='/'>
                    <HomePage />
                </Route> */}
                <Route path='/admin'>
                    {isAdmin === null ? <LoginPage login="admin"/> : <HomeAdmin />}
                </Route>
                <Route path='/add-test'>
                {isAdmin === null ? <LoginPage login="admin"/> : <AddTest />}
                    
                </Route>
                <Route path='/test'>
                {isAdmin === null ? <LoginPage login="admin"/> : <Test />}
                    
                </Route>
                <Route path='/student'>
                {isAdmin === null ? <LoginPage login="admin"/> : <Student />}
        
                </Route>
                <Route path='/edit-test/:id'>
                {isAdmin === null ? <LoginPage login="admin"/> : <EditTest />}

                </Route>
                <Route path='/question/:id'>
                {isAdmin === null ? <LoginPage login="admin"/> : <Question />}
                    
                </Route>
                <Route path='/result'>
                {isAdmin === null ? <LoginPage login="admin"/> : <ResultTest />}
                    
                </Route>
                <Route path='/go-test/:id'>
                {token === null ? <LoginPage login="user"/> : <GoTest />}
                    
                </Route>
                <Route path='/bai-thi'>
                {token === null ? <LoginPage login="user"/> : <ListTest/>}
                    
                </Route>
                <Route path='/login'>
                {token === null ? <LoginPage login="user"/> : <ListTest/>}
                </Route>
                {/* <Route path='/admin'>
                    {token !== null && decode.level === 2 ? <HomeAdmin /> : <Redirect to='/' />}
                    
                </Route>
                <Route path='/add-product'>
                {token !== null && decode.level === 2 ? <AddProduct /> : <Redirect to='/' />}
                </Route>
                <Route path='/list-product'>
                {token !== null && decode.level === 2 ? <ListProduct /> : <Redirect to='/' />}
                </Route>
                <Route path='/order-list'>
                {token !== null && decode.level === 2 ? <OrderList /> : <Redirect to='/' />}
                </Route>
                <Route path='/product/edit/:id'>
                {token !== null && decode.level === 2 ? <EditProduct /> : <Redirect to='/' />}
                </Route>
                <Route path='/category/:id'>
                    <CategoryPage />
                </Route>
                <Route exact path='/product/detail/:id/:cate' render={()=><ProductDetail />} />
                <Route path='/login'>
                    {token === null ? <LoginPage /> : <Redirect to='/' />}
                </Route>
                <Route path='/register'>
                    {token === null ? <RegisterPage /> : <Redirect to='/' />}
                </Route>
                <Route path='/cart'>
                    {token === null ? <Redirect to='/login' />: <CartPage />}
                </Route>
                <Route path='/payment'>
                    {token === null ? <Redirect to='/login' />: <PaymentPage />}
                </Route>
                <Route path='/order-infor'>
                    {token === null ? <Redirect to='/login' /> : <NotiOrder />}
                </Route>
                <Route path='/test/'>
                    <TestApp />
                </Route> */}
            </Suspense>
        </Router>
    )
}
export default HinShop;